.email {
  padding-top: 60px;
  padding-bottom: 60px;
  background-image: linear-gradient(
      -158deg,
      rgba(60, 26, 141, 0.85) 0%,
      rgba(117, 37, 167, 0.85) 100%
    ),
    url("../img/emailbg.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title {
    @include font-openSans(48px, $color-white, 300);
    @include mq("phone-wide") {
      font-size: 30px;
    }
  }
  &__subtitle {
    @include font-openSans(24px, $color-white, 300);
    line-height: 36px;
    margin-bottom: 20px;
    text-align: center;
    @include mq("phone-wide") {
      font-size: 18px;
      line-height: 24px;
    }
  }
  &__form {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  &__input {
    min-width: 280px;
    padding: 15px 20px;
    border-radius: 50px;
    font-size: 18px;
    border: none;
    &::placeholder {
      @include font-openSans(18px, #c3c2c2, 300);
    }
  }
  &__btn {
    position: absolute;
    right: 0;
    padding: 16px 25px;
    border: none;
    z-index: 1;
    transition: all 1s ease-in-out;
  }
}
