html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: $color-white;
  overflow-x: hidden;
  scrollbar-width: none;
}
html {
  scrollbar-width: none;
}
// :focus,
// :active {
//   outline: none;
// }

// a:focus,
// a:active {
//   outline: none;
// }

aside,
nav,
footer,
header,
section {
  display: block;
}

button {
  cursor: pointer;
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  width: 100%;
  max-width: 1200px;
}

.container {
  @include make-container(1270px, 15px);
}
.visually-hidden {
  @include visually-hidden;
}

.lock {
  overflow-y: hidden;
}
.main-btn {
  position: relative;
  color: $color-white;
  border-radius: 50px;
  background-image: linear-gradient(
    90deg,
    rgb(60, 221, 170),
    rgb(53, 207, 148),
    rgb(46, 193, 126),
    rgb(48, 207, 208),
    rgb(161, 140, 209),
    rgb(251, 194, 235),
    rgb(255, 98, 242)
  );
  background-size: 400%;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.25);
  transition: all 1.6s ease-in-out;
  z-index: 10;
  &:before {
    content: "";
    position: absolute;
    top: -5px;
    bottom: -5px;
    left: -5px;
    right: -5px;
    z-index: -10;
    background-image: linear-gradient(
      90deg,
      rgb(60, 221, 170),
      rgb(53, 207, 148),
      rgb(46, 193, 126),
      rgb(48, 207, 208),
      rgb(161, 140, 209),
      rgb(251, 194, 235),
      rgb(255, 36, 171)
    );
    background-size: 400%;
    border-radius: 50px;
    opacity: 1;
    transition: all 1s ease-in-out;
    filter: blur(10px);
  }
  &:hover {
    background-position: 100%;
    &:before {
      background-position: 100%;
    }
  }
}

.title {
  color: $color-black;
  font-size: 60px;
  font-weight: 300;
  text-align: center;
  letter-spacing: 0.5px;
}
::-webkit-scrollbar {
  display: none;
}
-ms-overflow-style {
  display: none;
}
.progressbar {
  font-size: 20px;
}
.scrollPath {
  position: fixed;
  top: 0;
  right: 0;
  width: 10px;
  height: 100%;
  background: rgba(104, 104, 104, 0.2);
  z-index: 100;
  @include mq("tablet") {
    width: 5px;
  }
}
.progressbar {
  position: fixed;
  top: 0;
  right: 0;
  width: 10px;
  height: 100%;
  background: linear-gradient(to top, #008aff, #00ffe7);
  z-index: 100;
  animation: animate 5s linear infinite;
  @include mq("tablet") {
    width: 5px;
  }
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to top, #008aff, #00ffe7);
    filter: blur(10px);
  }
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to top, #008aff, #00ffe7);
    filter: blur(30px);
  }
}

@keyframes animate {
  0%,
  100% {
    filter: hue-rotate(0deg);
  }
  50% {
    filter: hue-rotate(180deg);
  }
}
.pin-spacer {
  background-color: rgb(36, 41, 54);
}
