.opening {
  position: relative;
  background-image: $maingradient;
  min-height: 700px;
  &__background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url("../img/solarbg.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: bottom center;
  }
  &__edge {
    overflow: hidden;
    position: absolute;
    display: block;
    bottom: -14%;
    width: 100%;
    z-index: 1;
    fill: #fff;
    transform-origin: bottom;
    animation: animateWave 4000ms ease-in-out alternate infinite;
    @media (max-width: 1500px) {
      bottom: -5%;
    }
    @media (max-width: 580px) {
      width: 250%;
    }
  }
  &:after {
    @include pseudo;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: linear-gradient(
      0deg,
      rgba(14, 0, 78, 0.4),
      rgba(14, 0, 78, 0.4) 100%
    );
  }
  &:before {
    @include pseudo;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url("../img/mesh.png");
    background-size: 100% 75%;
    background-repeat: no-repeat;
    background-position: top center;
  }
  &__container {
    padding-top: 270px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 1;
    @include mq("phone-wide") {
      padding-top: 200px;
    }
  }
  &__top {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 80px;
    z-index: 1;
  }
  &__wrapper {
    width: 100%;
    position: relative;
    padding: 15px 50px;
    @include mq("tablet") {
      padding: 0;
    }
  }
  &__div {
    position: absolute;
    transition: 0.6s ease;
    @include mq("tablet") {
      display: none;
    }
    &-first {
      top: 0;
      left: 0;
      width: 30px;
      height: 3px;
      background-color: #fff;
      animation: firstDiv 12s infinite;
      animation-delay: 2s;
    }
    &-second {
      top: 0;
      left: 0;
      width: 3px;
      height: 30px;
      background-color: #fff;
      animation: secondDiv 12s infinite;
      animation-delay: 2s;
    }
    &-third {
      bottom: 0;
      right: 0;
      width: 3px;
      height: 30px;
      background-color: #fff;
      animation: thirdDiv 12s infinite;
      animation-delay: 2s;
    }
    &-fourth {
      bottom: 0;
      right: 0;
      width: 30px;
      height: 3px;
      background-color: #fff;
      animation: fourthDiv 12s infinite;
      animation-delay: 2s;
    }
  }
  &__title {
    @include font-roboto(30px, inherit, 700);
    text-transform: uppercase;
    text-align: center;
    margin-left: 40px;
    margin-bottom: 10px;
    @include mq("tablet") {
      font-size: 26px;
    }
  }
  &__subtitle {
    font-size: 27px;
    text-align: center;
    margin-left: 40px;
    @include mq("tablet") {
      font-size: 20px;
    }
  }
  &__text {
    @include font-openSans(32px, false, 300);
    margin-top: 60px;
    margin-bottom: 45px;
    text-align: center;
    @include mq("tablet") {
      font-size: 24px;
    }
  }
  &__subtext {
    max-width: 380px;
    @include font-openSans(18px, false, 300);
    text-align: center;
    @include mq("tablet") {
      font-size: 16px;
    }
  }
  &__svg {
    position: absolute;
    transform: translate(-50%, -50%);
  }
  &__images {
    width: 100%;
    position: relative;
    min-height: 500px;
    z-index: 1;
    @include mq("phone-wide") {
      display: none;
    }
  }
  &__bitcoin {
    top: 70%;
    left: 13%;
    max-width: 125px;
    max-height: 125px;
    animation: bitcoinFloating 2s alternate linear infinite;
    @include mq("tablet") {
      left: 11%;
    }
    @media (max-width: 415px) {
      left: 18%;
    }
  }
  &__house {
    width: 30%;
    max-height: 140px;
    min-width: 120px;
    object-fit: contain;
    top: 70px;
    left: 8%;
    z-index: 1;
    opacity: 0;
    @include mq("tablet") {
      left: 70px;
    }
  }
  &__tower {
    top: 80px;
    right: -7%;
    width: 13%;
    min-width: 90px;
    max-height: 208px;
    z-index: 1;
    opacity: 0;
    // @media (max-width: 1300px) {
    //   right: 0;
    // }
  }
  &__bighouse {
    left: 46%;
    bottom: -25%;
    width: 40%;
    min-width: 260px;
    height: 350px;
    z-index: 1;
    opacity: 0;
    @include mq("tablet") {
      left: 53%;
      bottom: -30%;
    }
  }
  &__topline {
    top: 50px;
    left: 53%;
    width: 70%;
    min-width: 300px;
    height: 6px;
    fill: none;
    stroke: #fff;
    stroke-linecap: round;
    stroke-width: 3px;
    stroke-dasharray: 0.001 6;
    transform: translate(-50%, -50%) scaleX(0);
    transform-origin: left;
  }
  &__rightline {
    width: 20%;
    height: 170px;
    fill: #fff;
    stroke: #fff;
    stroke-linecap: round;
    stroke-width: 3px;
    stroke-dasharray: 0.001 6;
    fill-rule: evenodd;
    top: 47%;
    right: 5%;
    transform: translate(-50%, -50%) rotate(15deg) scaleX(0);
    transform-origin: right;
    @include mq("tablet") {
      right: 0;
      top: 40%;
    }
  }
  &__leftLine {
    width: 220px;
    height: 170px;
    fill: #fff;
    stroke: #fff;
    stroke-linecap: round;
    stroke-width: 3px;
    stroke-dasharray: 0.001 6;
    fill-rule: evenodd;
    bottom: 20%;
    left: 23%;
    transform: translate(-50%, -50%) rotate(0deg) scaleX(0);
    transform-origin: right;
  }
  &__round {
    position: absolute;
    border-radius: 50%;
    background-image: linear-gradient(
      20deg,
      rgb(71, 29, 151) 0%,
      rgb(164, 56, 233) 100%
    );
    width: 90px;
    height: 90px;
    opacity: 0;
    @media (max-width: 990px) {
      width: 60px;
      height: 60px;
    }
    &:after {
      @include pseudo;
      width: 77.7%;
      height: 77.7%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border: 3.6px solid $color-white;
      border-radius: 50%;
    }
    &-middle {
      left: 50%;
      top: 50px;
    }
    &-left {
      left: 23%;
      top: 45%;
    }
    &-right {
      right: 18%;
      top: 48%;
      @include mq("tablet") {
        top: 40%;
        right: 10%;
      }
    }
  }
  &__icon {
    width: 40%;
    height: 30px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) П;
    transform-origin: top;
  }
}

@keyframes animateWave {
  0% {
    transform: scaleX(1);
  }
  100% {
    transform: scaleX(1.2);
  }
}

@keyframes bitcoinFloating {
  0% {
    transform: translate(-50%, -80px);
  }
  100% {
    transform: translate(-50%, -55px);
  }
}

@keyframes firstDiv {
  20% {
    left: 95.7%;
  }
  60% {
    left: 95.7%;
  }
  80% {
    left: 0;
  }
  100% {
    left: 0;
  }
}
@keyframes secondDiv {
  20% {
    top: 71%;
  }
  60% {
    top: 71%;
  }
  80% {
    top: 0;
  }
  100% {
    top: 0;
  }
}
@keyframes thirdDiv {
  20% {
    bottom: 71%;
  }
  60% {
    bottom: 71%;
  }
  80% {
    bottom: 0;
  }
  100% {
    bottom: 0;
  }
}
@keyframes fourthDiv {
  20% {
    right: 95.7%;
  }
  60% {
    right: 95.7%;
  }
  80% {
    right: 0;
  }
  100% {
    right: 0;
  }
}
