.overlay {
  position: fixed;
  display: none;
  opacity: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 500;
  background-image: linear-gradient(
    90deg,
    rgba(98, 0, 255, 0.7) 0%,
    rgba(89, 5, 141, 0.7) 100%
  );
  transform: translateY(-100%);
  transition: all 0.6s ease-in-out;
  &-done {
    opacity: 1;
    transform: translateY(0);
  }
  &-show {
    display: block;
  }
  &__close {
    position: absolute;
    right: 10%;
    top: 50px;
    width: 20px;
    height: 20px;
    background-color: transparent;
    &:hover {
      svg {
        transform: rotate(90deg);
      }
    }
    svg {
      width: 100%;
      height: 100%;
      fill: #fff;
      transition: all 0.4s ease-in-out;
    }
  }
  &__iframe {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 80%;
    transform: translate(-50%, -50%);
  }
}
