.footer {
  position: relative;
  background-color: rgb(36, 41, 54);
  &::before {
    @include pseudo;
    bottom: 0;
    height: 22%;
    width: 100%;
    background-color: rgb(30, 34, 45);
    @include mq("tablet") {
      height: 13%;
    }
  }
  &__container {
    padding: 35px 50px 0px;
  }
  &__list {
    padding: 40px 0 55px;
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    margin-bottom: 40px;
    &:first-child {
      width: 40%;
      @media (max-width: 840px) {
        width: 100%;
        border-right: none;
        padding-right: 0;
      }
    }
    &:not(:last-child) {
      border-right: 2px solid rgb(55, 62, 82);
      padding-right: 40px;
    }
    &:not(:first-child) {
      padding-left: 40px;
      text-align: center;
      width: 19%;
      @media (max-width: 840px) {
        width: 30%;
      }
      @include mq("tablet-small") {
        padding: 0;
        border: none;
        width: 48%;
      }
      @include mq("phone-wide") {
        width: 100%;
        margin-bottom: 20px;
      }
    }
  }
  &__title {
    margin-bottom: 30px;
    @include font-raleway(26px, #d3d3d3, 700);
  }
  &__text {
    @include font-raleway(13.4px, #ababab, 200);
    line-height: 26px;
  }
  &__img {
    margin-bottom: 20px;
    width: 40px;
    height: 30px;
  }
  &__topic {
    margin-bottom: 30px;
    @include font-raleway(20px, #d3d3d3, 700);
    @include mq("phone-wide") {
      margin-bottom: 10px;
    }
  }
  &__descr {
    @include font-raleway(15.6px, #dadada, 200);
    line-height: 26px;
  }
}

.top {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  border-bottom: 2px solid rgb(55, 62, 82);
  &__logo {
    width: 100px;
    height: 35px;
    margin-bottom: 25px;
    @include mq("phone") {
      width: 70px;
      height: 30px;
    }
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__wrapper {
    display: flex;
    margin-bottom: 10px;
  }
  &__link {
    @include font-raleway(16px, #757985, 200);
    color: #757985;
    transition: 0.3s ease-in-out;
    &:hover {
      filter: brightness(2);
      text-shadow: 2px 2px 3px rgb(255, 255, 255);
    }
    &:not(:last-child) {
      margin-right: 35px;
      @include mq("phone") {
        margin-right: 10px;
      }
    }
  }
}

.bottom {
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  z-index: 1;
  padding: 30px 0 20px;
  @include mq("phablet") {
    flex-direction: column;
    align-items: center;
    padding-bottom: 40px;
  }
  &__copyrights {
    @include font-raleway(13px, #757985, 200);
    position: absolute;
    bottom: 20px;
    left: 0;
  }
  &__link {
    @include font-raleway(16px, #a1a1a1, 400);
    text-transform: uppercase;
    transition: 0.3s ease-in-out;
    &:hover {
      filter: brightness(2);
      text-shadow: 2px 2px 3px rgb(255, 255, 255);
    }
    &:not(:last-child) {
      margin-right: 35px;
      @include mq("tablet") {
        margin-right: 10px;
      }
    }
    @include mq("tablet") {
      font-size: 12px;
    }
  }

  &__media {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 62px;
    height: 62px;
    border-radius: 50%;
    background-color: rgb(55, 62, 82);
    color: rgb(211, 211, 211);
    font-size: 20px;
    transition: 0.3s ease-in-out;
    &:hover {
      transform: scale(1.1);
      color: #bca0dc;
      background-color: #52307c;
    }
    &:not(:last-child) {
      margin-right: 35px;
      @include mq("tablet") {
        margin-right: 10px;
      }
    }
    @include mq("tablet") {
      width: 40px;
      height: 40px;
      margin-top: 10px;
    }
  }
}
