.work {
  padding-top: 120px;
  position: relative;
  overflow: hidden;
  &__span {
    font-weight: 500;
    color: rgb(78, 38, 172);
    background: -webkit-linear-gradient(rgb(78, 38, 172), rgb(200, 114, 255));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  &__list {
    margin-top: 100px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  &__item {
    width: 28%;
    margin-bottom: 100px;
    @media (max-width: 840px) {
      width: 46%;
    }
    @include mq("phone-wide") {
      width: 100%;
    }
  }
  &__subtitle {
    margin-bottom: 40px;
    font-size: 36px;
    line-height: 24px;
    font-weight: 300;
    color: #784cc6;
    text-transform: uppercase;
  }
  &__text {
    font-size: 20px;
    color: #9f9f9f;
  }
  &__canvas {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -10;
  }
}
