.intro {
  padding-bottom: 100px;
  @include mq("phone-wide") {
    padding-top: 33px;
  }
  &__wrapper {
    margin-top: 20px;
    margin-bottom: 120px;
    position: relative;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 30px;
    z-index: 2;
    @media (max-width: 1050px) {
      justify-content: center;
    }
    @include mq("phone-wide") {
      display: none;
    }
  }
  &__manimg {
    position: absolute;
    width: 130px;
    height: 130px;
    top: -50px;
    left: 25%;
    img {
      width: 100%;
      height: 100%;
    }
    @media (max-width: 1050px) {
      left: 20%;
    }
    @media (max-width: 660px) {
      left: 12%;
    }
    @media (max-width: 520px) {
      left: 7%;
    }
  }
  &__coinimg {
    position: absolute;
    width: 50px;
    height: 50px;
    left: 48%;
    top: 0;
    img {
      width: 100%;
      height: 100%;
    }
    @media (max-width: 860px) {
      top: 10px;
      left: 50%;
    }
    @media (max-width: 660px) {
      left: 52%;
    }
    @media (max-width: 520px) {
      left: 54%;
    }
  }
  &__arrow {
    position: absolute;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__arrowup {
    left: 47.5%;
    top: -50px;
    width: 25px;
    height: 40px;
    animation: ArrowFloatingY 1.4s alternate linear infinite;
    @media (max-width: 660px) {
      left: 50%;
    }
  }
  &__arrowbot {
    left: 51.5%;
    top: -50px;
    width: 25px;
    height: 40px;
    animation: ArrowFloatingY 1.4s alternate linear infinite;
    animation-delay: -4.3s;
    @media (max-width: 660px) {
      left: 56%;
    }
  }
  &__arrowleft {
    left: 41.5%;
    top: 35px;
    width: 40px;
    height: 25px;
    animation: ArrowFloatingX 1.4s alternate linear infinite;
  }
  &__arrowright {
    left: 41.5%;
    top: 0px;
    width: 40px;
    height: 25px;
    animation: ArrowFloatingX 1.4s alternate linear infinite;
    animation-delay: -4.3s;
  }
  &__link {
    display: inline-block;
    @include font-roboto(22px, #5923a6, 400);
    border: 3px solid rgb(89, 35, 166);
    border-radius: 50px;
    padding: 15px 40px;
    transition: all 0.5s linear;
    @media (max-width: 1050px) {
      padding: 10px 20px;
      margin-top: 100px;
      background: none;
      margin-right: 10px;
    }
    &-first {
      margin-right: 55px;
    }
    &-second {
      padding-right: 85px;
      background-image: url("../img/whale.png");
      background-position: center right 30px;
      background-repeat: no-repeat;
      background-size: 40px 40px;
      background-color: transparent;
    }
    &:hover {
      color: #fff;
      background-color: #5923a6;
    }
  }
  &__svg {
    transform: translate(-50%, -50%);
    width: 27px;
    height: 42px;
    fill: $maingradient;
    z-index: 10;
  }
  &__block-text {
    width: 100%;
    padding: 0 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include mq("tablet") {
      padding: 0;
    }
  }
  &__title {
    margin-bottom: 30px;
    max-width: 540px;
    text-align: center;
    @include font-roboto(30px, $color-tundora, 400);
    @include mq("phone-wide") {
      font-size: 22px;
    }
  }
  &__text {
    margin-bottom: 45px;
    text-align: center;
    @include font-openSans(24px, $color-chalise, 300);
    font-style: italic;
    @include mq("phone-wide") {
      font-size: 16px;
    }
  }
  &__ref {
    padding: 20px 40px;
    @include font-roboto(20px, $color-white, 500);
  }
}

.payment {
  z-index: 100;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(
    -20deg,
    rgb(71, 29, 151) 0%,
    rgb(164, 56, 233) 100%
  );
  transform: translateY(-100%);
  transition: transform 0.5s ease-out;
  &-show {
    transform: translateY(0);
  }
  &__form {
    padding: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__text {
    padding-bottom: 10px;
  }
  &__label {
    &:nth-child(even) {
      margin-left: 20px;
    }
  }
  &__input {
    padding: 5px;
    border-radius: 20px;
    border: none;
  }
  &__wrapper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
  }
}
@keyframes ArrowFloatingY {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}
@keyframes ArrowFloatingX {
  0% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(-10px);
  }
}
