.team {
  position: relative;
  &__container {
    padding-top: 100px;
    padding-bottom: 100px;
  }
  &__title {
    margin-bottom: 65px;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  &__item {
    position: relative;
    width: 21%;
    height: 340px;
    margin-bottom: 70px;
    transform-style: preserve-3d;
    z-index: 10;
    @media (max-width: 840px) {
      width: 30%;
    }
    @media (max-width: 840px) {
      width: 45%;
    }
    &:hover {
      .team__front {
        transform: perspective(500px) rotateY(180deg);
      }
      .team__back {
        transform: perspective(500px) rotateY(360deg);
      }
    }
  }
  &__card {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 14% 0px;
    box-shadow: 0px 2px 13px 0px rgba(0, 0, 0, 0.25);
    transform-style: preserve-3d;
    backface-visibility: hidden;
    transform: perspective(500px) rotateY(0deg);
    transition: 1s ease-in-out;
  }
  &__back {
    background-image: linear-gradient(
      -110deg,
      rgb(156, 10, 251) 0%,
      rgb(187, 82, 255) 100%
    );
    transform: perspective(500px) rotateY(180deg);
    &-text {
      line-height: 24px;
      margin-bottom: 30px;
      @include mq("phone-wide") {
        line-height: 16px;
        font-size: 14px;
      }
    }
    &-link {
      display: inline-block;
      color: #fff;
      font-size: 28px;
      transition: color 0.3s ease-in-out;
      &:not(:last-child) {
        margin-right: 20px;
      }
      &:hover {
        color: palevioletred;
      }
    }
  }
  &__img {
    margin-bottom: 20px;
    position: relative;
    overflow: hidden;
    border-radius: 50%;
    width: 100px;
    height: 100px;
    transform: perspective(500px) translateZ(50px);
    @include mq("phone-wide") {
      width: 80px;
      height: 80px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &::before {
      @include pseudo;
      width: 100%;
      height: 100%;
      background-image: linear-gradient(
        -158deg,
        rgba(46, 19, 109, 0.5) 0%,
        rgba(117, 37, 167, 0.5) 100%
      );
    }
    &::after {
      @include pseudo;
      width: 93%;
      height: 93%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border: 3px solid $color-white;
      border-radius: 50%;
    }
  }
  &__name {
    position: relative;
    font-size: 16px;
    font-weight: 500;
    color: #5a5a5a;
    letter-spacing: 0.5px;
    padding-bottom: 20px;
    margin-bottom: 15px;
    transform: perspective(500px) translateZ(50px);
    &::after {
      @include pseudo;
      bottom: 0;
      left: 50%;
      transform: translate(-50%, -50%);
      height: 4px;
      min-width: 64px;
      background-image: linear-gradient(
        -110deg,
        rgb(156, 10, 251) 0%,
        rgb(187, 82, 255) 100%
      );
    }
  }
  &__job {
    text-align: center;
    font-size: 16px;
    color: #5a5959;
    letter-spacing: 0.5px;
    margin-bottom: 15px;
  }
}
