.features {
  position: relative;
  &__container {
    padding: 100px 0px 40px;
  }
  &__title {
    margin-bottom: 70px;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  &__img {
    width: 60%;
    height: auto;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    @include mq("phone-wide") {
      width: 40%;
    }
    svg {
      width: 100%;
      height: 100%;
    }
  }
  &__item {
    margin-bottom: 120px;
    width: 26%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: black;
    @include mq("tablet") {
      width: 46%;
    }
    @include mq("phone-wide") {
      width: 100%;
      margin-bottom: 60px;
    }
  }
  &__subtitle {
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 24px;
    color: #5a5a5a;
    letter-spacing: 0.5px;
    text-align: center;
  }
  &__text {
    font-size: 18px;
    color: #7f7e7f;
    text-align: center;
    letter-spacing: 0.5px;
  }
  &__canvas {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -10;
  }
}
