.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 60px;
  z-index: 100;
  transition: all 0.5s ease;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -10;
    background-image: linear-gradient(
      90deg,
      rgba(71, 29, 151, 0.4) 0%,
      rgba(164, 56, 233, 0.4) 100%
    );
    opacity: 0;
    transition: opacity 0.5s ease;
  }

  &-active {
    padding-top: 10px;
    padding-bottom: 10px;
    box-shadow: 0 0.3px 0.7px rgba(0, 0, 0, 0.126),
      0 0.9px 1.7px rgba(0, 0, 0, 0.179), 0 1.8px 3.5px rgba(0, 0, 0, 0.224),
      0 3.7px 7.3px rgba(0, 0, 0, 0.277), 0 10px 20px rgba(0, 0, 0, 0.4);
    backdrop-filter: blur(10px);
    &:before {
      opacity: 1;
    }
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 40px;
  }
  &__logo {
    display: inline-block;
    width: 170px;
    height: 60px;
    z-index: 50;
  }
  &__svg {
    width: 100%;
    height: 100%;
  }
  &__nav {
    margin-top: 15px;
  }
}

.nav {
  &__list {
    display: flex;
    @media (max-width: 992px) {
      position: absolute;
      width: 100%;
      height: 100vh;
      left: 0;
      top: 0;
      z-index: 30;
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 100vh;
      padding-top: 170px;
      background-image: linear-gradient(
        20deg,
        rgb(71, 29, 151) 0%,
        rgb(164, 56, 233) 100%
      );
      transform: translateX(100%);
      transition: transform 0.7s ease-out;
    }
    &-show {
      transform: translateX(0) !important;
    }
  }
  &__item {
    &:not(:last-child) {
      margin-right: 63px;
    }
    @media (max-width: 992px) {
      margin-right: 0 !important;
      padding-bottom: 40px;
    }
  }
  &__link {
    position: relative;
    display: inline-block;
    @include font-roboto(18px, false, 500);
    text-transform: uppercase;
    color: $color-white;
    background: linear-gradient(
      to right,
      (rgb(78, 38, 172), rgb(200, 114, 255)) 50%,
      #fff 50%
    );
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-size: 200% 100%;
    background-position: 100%;
    transition: background-position 0.3s linear;
    animation-delay: 1s;
    &::after {
      @include pseudo;
      bottom: -20px;
      left: 50%;
      transform: translate(-50%, -50%) scaleX(0);
      height: 4px;
      width: 50px;
      background-image: linear-gradient(
        -110deg,
        rgb(156, 10, 251) 0%,
        rgb(187, 82, 255) 100%
      );
      transform-origin: left;
      transition: transform 0.3s linear;
    }
    &:hover {
      background-position: 0 100%;
      &::after {
        transform: translate(-50%, -50%) scaleX(1);
      }
    }
    @media (max-width: 992px) {
      opacity: 0;
    }
  }
}

.burger {
  display: none;
  z-index: 50;
  @media (max-width: 992px) {
    position: relative;
    align-self: center;
    width: 30px;
    height: 30px;
    display: block;
  }
  &__line1 {
    position: absolute;
    width: 100%;
    height: 2px;
    top: 8px;
    background-color: #fff;
    transition: transform 0.5s ease;
  }
  &__line2 {
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 8px;
    background-color: #fff;
    transition: all 0.5s ease;
  }
  &-show {
    .burger__line1 {
      transform: rotate(135deg) translateY(-8.5px);
    }
    .burger__line2 {
      transform: rotate(225deg) translateY(8.5px);
    }
  }
}

@keyframes navLinkFade {
  from {
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
