.video {
  position: relative;
  width: 100%;
  height: 350px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("../img/videobg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  &:after {
    @include pseudo;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
      -45deg,
      rgba(161, 140, 209, 0.6),
      rgba(251, 194, 235, 0.6),
      rgba(67, 233, 123, 0.6),
      rgba(48, 207, 208, 0.6),
      rgba(118, 75, 162, 0.6),
      rgba(254, 225, 64, 0.6)
    );
    background-size: 400% 400%;
    animation: gradient 13s ease-in-out infinite;
  }
  &__link {
    display: block;
    width: 90px;
    height: 115px;
    z-index: 1;
  }
  &__svg {
    width: 100%;
    height: 100%;
    fill: $color-white;
    pointer-events: none;
    z-index: 1;
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
