.more {
  background-image: linear-gradient(
      -158deg,
      rgba(60, 26, 141, 0.8) 0%,
      rgba(117, 37, 167, 0.8) 100%
    ),
    url("../img/videobg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  &__container {
    position: relative;
    padding: 150px 160px;
    @media (max-width: 1200px) {
      padding: 150px 10px;
    }
  }
  &__shape {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @media (max-width: 920px) {
      left: 6%;
    }
    @include mq("tablet-small") {
      left: 20%;
    }
    &-animatedsvg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 0;
      background-image: url("../img/moreshape.svg");
      background-repeat: no-repeat;
      background-position: top center;
      z-index: 1;
    }
  }
  &__round {
    position: absolute;
    top: 130px;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    background-image: linear-gradient(
      20deg,
      rgb(71, 29, 151) 0%,
      rgb(164, 56, 233) 100%
    );
    width: 80px;
    height: 80px;
    z-index: 10;
    &:after {
      @include pseudo;
      width: 80%;
      height: 80%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border: 3px solid $color-white;
      border-radius: 50%;
    }
    &:before {
      content: "1";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 30px;
      color: #f6b545;
      font-weight: 500;
      font-family: "Roboto", sans-serif;
    }

    &:nth-child(2) {
      top: 290px;
      &:before {
        content: "2";
      }
    }
    &:nth-child(3) {
      top: 430px;
      &:before {
        content: "3";
      }
    }
    &:nth-child(4) {
      top: 590px;
      &:before {
        content: "4";
      }
    }
  }
  &__list {
    display: flex;
    flex-direction: column;
    @media (max-width: 920px) {
      margin-left: 100px;
    }
    @include mq("tablet-small") {
      margin-left: 0;
    }
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include mq("tablet-small") {
      flex-direction: column;
      align-items: flex-end;
    }
    &:not(:last-child) {
      margin-bottom: 50px;
    }
  }
  &__subtitle {
    display: inline-block;
    font-size: 24px;
    color: #f6b545;
    font-weight: 500;
    text-align: right;
    width: 40%;
    max-width: 400px;
    @include mq("tablet-small") {
      margin-left: 0;
      margin-bottom: 10px;
      font-size: 20px;
      width: 60%;
      &:nth-child(2n) {
        order: -1;
      }
    }
  }
  &__text {
    width: 58%;
    max-width: 400px;
    font-size: 18px;
    color: $color-white;
    text-align: right;
    @include mq("tablet") {
      font-size: 14px;
    }
    @include mq("tablet-small") {
      &:nth-child(2n) {
        text-align: right;
      }
    }
    &:nth-child(2n) {
      text-align: left;
    }
  }
}
